.image-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 columns */
  gap: 10px; /* Adjust the gap between the images */
  padding: 20px; /* Add padding to the grid */
}

.image-grid-item {
  position: relative;
  padding-bottom: 100%; /* Maintain aspect ratio */
  background-size: cover;
  background-position: center;
  overflow: hidden;
}

.image-grid-item:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
  opacity: 0; /* Hide the hover image by default */
}

.image-grid-item.hovered:before,
.image-grid-item:hover:before {
  opacity: 1; /* Show the hover image on hover */
  transform: scale(1.1); /* Slight zoom effect */
}

/* Define each grid item's background and hover image */
.image-grid-item:nth-child(1) {
  background-image: url('../images/gridimg/grid1.jpg');
}

.image-grid-item:nth-child(1):before {
  background-image: url('../images/gridhoverimg/grdhvr1.jpg');
}

