.banner-slide-1 {
    background-image: url('../images/bannerbg.jpg'); /* Adjust the path as needed */
  }
  
  .banner-slide-2 {
    background-image: url('../images/bannerbg2.jpg'); /* Adjust the path as needed */
  }
  
  .banner-slide-3 {
    background-image: url('../images/bannerbg3.jpg'); /* Adjust the path as needed */
  }
  
  .banner-slide {
    background-size: cover;
    background-position: center;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    text-align: center;
    padding: 20px;
  }
  
  @media (max-width: 768px) {
    .banner-slide {
      height: 200px;
    }
  }
  
  @media (max-width: 480px) {
    .banner-slide {
      height: 150px;
    }
  }
  
  .banner-content {
    background-color: rgba(0, 0, 0, 0.5);  /* Optional: to make text stand out */
    padding: 20px;
    border-radius: 10px;
  }
  