.floating-message-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black overlay */
    z-index: 1000; /* Ensure it's above other content */
  }
  
  .floating-message-content {
    text-align: center;
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  }
  .welcome-message-container {
    background-color: rgba(0, 0, 255, 0.8); /* Example color for welcome */
    color: white;
  }
  
  .close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px;
    cursor: pointer;
  }
  
  .message-icon {
    width: 100px; /* Adjust image size as needed */
    height: auto;
    margin-bottom: 20px;
  }
  
  .message-text {
    font-size: 18px;
    color: #333;
  }
  

  .success-message {
    font-size: 18px;
    font-weight: bold;
  }
  
  .success-message-container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 255, 0, 0.8); /* Example color */
    color: white;
    padding: 20px;
    border-radius: 8px;
    z-index: 1000;
  }